interface EntranceActionsProps {
  disabled?: boolean;
  onNavigate: () => void;
  onShare: () => void;
  largeButtons: boolean;
}

const EntranceActions = ({ disabled, onNavigate, onShare, largeButtons }: EntranceActionsProps) => {
  const base = "flex-1 rounded-full font-semibold focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2";
  const sizeClasses = largeButtons ? "py-4 text-base" : "py-3 text-sm";
  return (
    <div className={`flex gap-3 ${largeButtons ? "mt-4" : "mt-2"}`}>
      <button
        className={`${base} ${sizeClasses} bg-[#26D0A4] text-[#0F1A2A]`}
        onClick={onNavigate}
        disabled={disabled}
        aria-label="Navigate to entrance"
      >
        Navigate
      </button>
      <button
        className={`${base} ${sizeClasses} bg-[#13243a] text-[#EAF2F8]`}
        onClick={onShare}
        disabled={disabled}
        aria-label="Share entrance link"
      >
        Share door
      </button>
    </div>
  );
};

export default EntranceActions;
