export function getBaseStyle(highContrast: boolean) {
  return {
    version: 8,
    name: "ClearPath Base",
    sources: {
      osm: {
        type: "raster",
        tiles: ["https://tile.openstreetmap.org/{z}/{x}/{y}.png"],
        tileSize: 256,
        attribution:
          'Map data <a href="https://www.openstreetmap.org/copyright">OpenStreetMap contributors</a>'
      }
    },
    sprite: undefined,
    glyphs: undefined,
    layers: [
      {
        id: "background",
        type: "background",
        paint: {
          "background-color": highContrast ? "#ffffff" : "#0b1323"
        }
      },
      {
        id: "osm-tiles",
        type: "raster",
        source: "osm",
        minzoom: 0,
        maxzoom: 19,
        paint: {
          "raster-brightness-min": highContrast ? 0 : 0.06,
          "raster-brightness-max": highContrast ? 1 : 0.95,
          "raster-contrast": highContrast ? 0.6 : 0.2,
          "raster-saturation": highContrast ? 0 : -0.1
        }
      }
    ]
  };
}
