export function openNavigation(lat: number, lon: number) {
  const apple = `https://maps.apple.com/?daddr=${lat},${lon}`;
  const google = `https://www.google.com/maps/dir/?api=1&destination=${lat},${lon}`;
  const isApple = /iPad|iPhone|Macintosh/.test(navigator.userAgent);
  const url = isApple ? apple : google;
  window.open(url, "_blank", "noopener");
}

export async function shareDoor(link: string) {
  if (navigator.share) {
    try {
      await navigator.share({ title: "ClearPath entrance", url: link });
      return true;
    } catch {
      // fallback to clipboard
    }
  }
  if (navigator.clipboard) {
    await navigator.clipboard.writeText(link);
  }
  return false;
}
