import { EntranceResult, PlaceSuggestion, QueuedAction } from "../types";
import {
  cacheEntrance,
  clearQueuedAction,
  getCachedEntrance,
  getQueuedActions,
  queueAction
} from "./storage";

const BASE_HEADERS = {
  "Content-Type": "application/json"
};

let lastSuggestTs = 0;
let pendingController: AbortController | null = null;

export async function suggestPlaces(
  query: string,
  coords?: { lat: number; lon: number }
): Promise<PlaceSuggestion[]> {
  const now = Date.now();
  if (now - lastSuggestTs < 250) {
    await new Promise((resolve) => setTimeout(resolve, 250 - (now - lastSuggestTs)));
  }
  lastSuggestTs = Date.now();
  pendingController?.abort();
  pendingController = new AbortController();
  const params = new URLSearchParams({ q: query });
  if (coords) {
    params.set("lat", String(coords.lat));
    params.set("lon", String(coords.lon));
  }
  const response = await fetch(`/api/geocode/suggest?${params.toString()}`, {
    headers: BASE_HEADERS,
    signal: pendingController.signal
  });
  if (!response.ok) {
    throw new Error("Suggestion request failed");
  }
  return response.json();
}

export async function fetchEntrance(query: string): Promise<EntranceResult> {
  const params = new URLSearchParams({ q: query });
  const response = await fetch(`/api/entrance?${params.toString()}`, {
    headers: BASE_HEADERS
  });
  if (!response.ok) {
    const cached = await getCachedEntrance(query);
    if (cached?.payload) {
      return cached.payload as EntranceResult;
    }
    throw new Error("Entrance lookup failed");
  }
  const json = (await response.json()) as EntranceResult;
  await cacheEntrance(json.id, json);
  return json;
}

async function sendAction(url: string, payload: object) {
  const response = await fetch(url, {
    method: "POST",
    headers: BASE_HEADERS,
    body: JSON.stringify(payload)
  });
  if (!response.ok) {
    throw new Error("Request failed");
  }
  return response.json();
}

export async function confirmEntrance(payload: {
  placeId: string;
  entrance: { lat: number; lon: number };
}) {
  try {
    const result = await sendAction("/api/confirm", payload);
    return { ok: true, result };
  } catch (error) {
    const action: QueuedAction = {
      type: "confirm",
      payload,
      createdAt: Date.now()
    };
    await queueAction(action);
    return { ok: false, queued: true, error };
  }
}

export async function correctEntrance(payload: {
  placeId: string;
  entrance: { lat: number; lon: number; accessible: boolean };
}) {
  try {
    const result = await sendAction("/api/correct", payload);
    return { ok: true, result };
  } catch (error) {
    const action: QueuedAction = {
      type: "correct",
      payload,
      createdAt: Date.now()
    };
    await queueAction(action);
    return { ok: false, queued: true, error };
  }
}

export async function replayQueuedActions() {
  const actions = await getQueuedActions();
  for (const action of actions) {
    try {
      if (action.type === "confirm") {
        await sendAction("/api/confirm", action.payload);
      } else if (action.type === "correct") {
        await sendAction("/api/correct", action.payload);
      }
      await clearQueuedAction(action.createdAt);
    } catch {
      // keep in queue on failure
    }
  }
}
