import { EntranceResult } from "../types";

interface BottomSheetProps {
  place: EntranceResult | null;
  status?: { tone: "mint" | "amber" | "coral"; message: string } | null;
  offline: boolean;
  verifiedTotal: number;
  localVerified: number;
}

const toneColors = {
  mint: "text-[#26D0A4]",
  amber: "text-[#E8A74B]",
  coral: "text-[#E85D5A]"
};

const BottomSheet = ({ place, status, offline, verifiedTotal, localVerified }: BottomSheetProps) => {
  if (!place) return null;
  return (
    <section
      aria-label="Arrival details"
      className="pointer-events-auto mx-auto mt-4 max-w-[860px] rounded-3xl bg-[rgba(9,23,44,0.88)] px-6 py-5 text-[#EAF2F8] backdrop-blur"
    >
      <div className="flex items-center justify-between gap-3">
        <div>
          <h2 className="text-lg font-semibold">Arrival details</h2>
          {status ? (
            <p className={`text-xs ${toneColors[status.tone]}`}>{status.message}</p>
          ) : (
            <p className="text-xs text-[#8EA0BA]">
              {offline ? "Offline mode - using saved entrances" : "Entrance refreshed"}
            </p>
          )}
        </div>
        <div className="rounded-full bg-[#13243a] px-4 py-2 text-xs text-[#A7B7CE]">
          Verified {verifiedTotal + localVerified} times
        </div>
      </div>
      <div className="mt-4 grid gap-4 lg:grid-cols-2">
        <article className="rounded-3xl bg-[#0E1C30] px-4 py-4 text-sm shadow-sm">
          <header className="flex items-center justify-between text-xs uppercase tracking-wide text-[#8EA0BA]">
            <span>Verified entrance</span>
            <span>{friendlyConfidence(place.entrance.confidence)}</span>
          </header>
          <p className="mt-2 text-base font-semibold">{place.name}</p>
          <p className="mt-1 text-xs text-[#8EA0BA]">
            {place.method.replace(/_/g, " ")} | {place.entrance.lat.toFixed(5)} deg, {place.entrance.lon.toFixed(5)} deg
          </p>
          {place.lastVerifiedAt && (
            <p className="mt-1 text-xs text-[#8EA0BA]">
              Last verified {new Date(place.lastVerifiedAt).toLocaleString()}
            </p>
          )}
        </article>
        <article className="rounded-3xl bg-[#0E1C30] px-4 py-4 text-sm shadow-sm">
          <header className="flex items-center justify-between text-xs uppercase tracking-wide text-[#8EA0BA]">
            <span>Drop-off suggestion</span>
            <span>Closest public roadway access</span>
          </header>
          {place.dropoff ? (
            <p className="mt-2 text-sm">
              Lat {place.dropoff.lat.toFixed(5)} deg, Lon {place.dropoff.lon.toFixed(5)} deg
            </p>
          ) : (
            <p className="mt-2 text-sm text-[#8EA0BA]">No curb drop-off detected</p>
          )}
          <p className="mt-2 text-xs text-[#8EA0BA]">
            Drawn from local streets and walkways. Double-check signage on arrival.
          </p>
        </article>
      </div>
      <details className="mt-5 rounded-2xl bg-[#0E1C30] px-4 py-4 text-sm text-[#A7B7CE] shadow-sm">
        <summary className="cursor-pointer text-sm font-semibold text-[#EAF2F8]">
          How this pin was chosen
        </summary>
        <p className="mt-3">
          ClearPath analyzes OpenStreetMap sidewalks and roads inside the building&apos;s footprint,
          then projects the most walkable point to the curb. Corrections from neighbors refine the
          location for everyone.
        </p>
        <a
          href="/#/readme"
          className="mt-3 inline-flex text-sm font-semibold text-[#26D0A4] underline"
        >
          Learn more
        </a>
      </details>
    </section>
  );
};

function friendlyConfidence(confidence: EntranceResult["entrance"]["confidence"]) {
  if (confidence === "high") return "High confidence";
  if (confidence === "medium") return "Likely";
  return "Needs confirmation";
}

export default BottomSheet;
