import {
  ChangeEvent,
  KeyboardEvent,
  useEffect,
  useMemo,
  useRef,
  useState
} from "react";
import { PlaceSuggestion } from "../types";

interface SearchBoxProps {
  value: string;
  onChange: (value: string) => void;
  onSubmit: (value: string) => void;
  suggestions: PlaceSuggestion[];
  loading: boolean;
  error?: string | null;
  onSelect: (suggestion: PlaceSuggestion) => void;
  onActivateMap: () => void;
  recent: PlaceSuggestion[];
}

const SearchBox = ({
  value,
  onChange,
  onSubmit,
  suggestions,
  loading,
  error,
  onSelect,
  onActivateMap,
  recent
}: SearchBoxProps) => {
  const inputRef = useRef<HTMLInputElement | null>(null);
  const [highlighted, setHighlighted] = useState(0);
  const [touched, setTouched] = useState(false);

  useEffect(() => {
    if (value.length > 0 && !touched) {
      setTouched(true);
      onActivateMap();
    }
  }, [value, touched, onActivateMap]);

  const list = useMemo(() => {
    if (suggestions.length) return suggestions;
    if (!value && recent.length) return recent;
    return [];
  }, [suggestions, recent, value]);

  useEffect(() => {
    setHighlighted(0);
  }, [value, suggestions]);

  function handleChange(event: ChangeEvent<HTMLInputElement>) {
    onChange(event.target.value);
  }

  function handleKey(event: KeyboardEvent<HTMLInputElement>) {
    if (event.key === "ArrowDown") {
      event.preventDefault();
      setHighlighted((prev) => (prev + 1) % list.length);
    } else if (event.key === "ArrowUp") {
      event.preventDefault();
      setHighlighted((prev) => (prev - 1 + list.length) % list.length);
    } else if (event.key === "Enter") {
      event.preventDefault();
      if (list.length) {
        onSelect(list[highlighted]);
      } else {
        onSubmit(value);
      }
    }
  }

  return (
    <div className="relative mt-4">
      <label className="sr-only" htmlFor="clearpath-search">
        Search for a building entrance
      </label>
      <div className="relative flex items-center">
        <input
          id="clearpath-search"
          ref={inputRef}
          value={value}
          onChange={handleChange}
          onKeyDown={handleKey}
          onFocus={onActivateMap}
          placeholder="Search any building entrance"
          className="h-12 w-full rounded-full bg-[#0E1C30] pl-4 pr-14 text-sm text-[#EAF2F8] placeholder:text-[#8897AD] shadow-sm focus:outline-none focus:ring-2 focus:ring-mint-500"
          aria-autocomplete="list"
          aria-expanded={Boolean(list.length)}
        />
        {value && (
          <button
            className="absolute right-3 flex h-9 w-9 items-center justify-center rounded-full bg-[#15263F] text-[#EAF2F8] focus:outline-none focus:ring-2 focus:ring-mint-500"
            onClick={() => onChange("")}
            aria-label="Clear search"
          >
            Clear
          </button>
        )}
      </div>
      <div className="relative z-10">
        {loading && (
          <div className="absolute left-4 top-3 text-xs text-[#A7B7CE]" aria-live="polite">
            Searching...
          </div>
        )}
        {error && (
          <div className="mt-3 rounded-2xl bg-[#2a1a1a]/80 px-4 py-3 text-sm text-[#E85D5A]">
            {error}
          </div>
        )}
        {!!list.length && (
          <div className="mt-3 rounded-3xl bg-[#0E1C30]/95 shadow-sm">
            <ul role="listbox" aria-label="Search suggestions">
              {list.map((suggestion, index) => (
                <li key={suggestion.id}>
                  <button
                    type="button"
                    onClick={() => onSelect(suggestion)}
                    onMouseEnter={() => setHighlighted(index)}
                    className={`flex w-full items-center justify-between px-4 ${
                      index === highlighted ? "bg-[#13243A]" : ""
                    }`}
                    role="option"
                    aria-selected={index === highlighted}
                  >
                    <div className="flex flex-col py-4 text-left">
                      <span className="text-base font-semibold text-[#EAF2F8]">
                        {suggestion.name}
                      </span>
                      <span className="text-xs text-[#8EA0BA]">{suggestion.context}</span>
                    </div>
                    {suggestion.distanceMeters !== undefined && (
                      <span className="text-xs text-[#8EA0BA]">
                        {formatDistance(suggestion.distanceMeters)}
                      </span>
                    )}
                  </button>
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
};

function formatDistance(distance: number) {
  if (distance > 1000) {
    return `${(distance / 1000).toFixed(1)} km`;
  }
  return `${distance} m`;
}

export default SearchBox;
