interface ConfirmNudgeProps {
  isActive: boolean;
  onConfirm: () => void;
  onAdjust: () => void;
  onSave: () => void;
  onCancelAdjust: () => void;
  canSave: boolean;
  adjustMode: boolean;
  largeButtons: boolean;
  requireAccessible: boolean;
  entranceAccessible: boolean;
  onAddAccessible: () => void;
  onToggleAccessible: () => void;
}

const ConfirmNudge = ({
  isActive,
  onConfirm,
  onAdjust,
  onSave,
  onCancelAdjust,
  canSave,
  adjustMode,
  largeButtons,
  requireAccessible,
  entranceAccessible,
  onAddAccessible,
  onToggleAccessible
}: ConfirmNudgeProps) => {
  const baseButton =
    "flex-1 rounded-full font-semibold transition focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2";
  const size = largeButtons ? "py-4 text-base" : "py-3 text-sm";
  return (
    <div className="mt-4 flex flex-col gap-3">
      {requireAccessible && !entranceAccessible && (
        <div className="rounded-2xl bg-[#E85D5A]/10 px-4 py-3 text-sm text-[#EAF2F8]">
          <div className="flex items-center justify-between gap-3">
            <span>No wheelchair entrance found, add one to help neighbors</span>
            <button
              className="rounded-full bg-[#26D0A4] px-3 py-2 text-xs font-semibold text-[#0F1A2A] focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2 focus:ring-offset-[#E85D5A]/10"
              onClick={onAddAccessible}
            >
              Add accessible entrance
            </button>
          </div>
        </div>
      )}
      {!adjustMode && (
        <div className="flex gap-3">
          <button
            disabled={!isActive}
            className={`${baseButton} ${size} bg-[#26D0A4] text-[#0F1A2A] disabled:opacity-60`}
            onClick={onConfirm}
          >
            Confirm door
          </button>
          <button
            className={`${baseButton} ${size} bg-[#13243a] text-[#EAF2F8]`}
            onClick={onAdjust}
          >
            Adjust door
          </button>
        </div>
      )}
      {adjustMode && (
        <div className="flex flex-col gap-3">
          <div className="flex items-center justify-between rounded-2xl bg-[#0E1C30] px-4 py-3 text-sm text-[#EAF2F8]">
            <span className="font-semibold">Accessible entrance</span>
            <button
              type="button"
              className={`flex h-8 w-14 items-center rounded-full p-1 transition ${
                entranceAccessible ? "bg-[#26D0A4]" : "bg-[#24344d]"
              }`}
              onClick={onToggleAccessible}
            >
              <span
                className={`h-6 w-6 rounded-full bg-white transition ${
                  entranceAccessible ? "translate-x-6" : ""
                }`}
              />
            </button>
          </div>
          <div className="flex gap-3">
            <button
              className={`${baseButton} ${size} bg-[#26D0A4] text-[#0F1A2A] disabled:opacity-60`}
              disabled={!canSave}
              onClick={onSave}
            >
              Save
            </button>
            <button
              className={`${baseButton} ${size} bg-[#E85D5A] text-white`}
              onClick={onCancelAdjust}
            >
              Cancel
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ConfirmNudge;
