interface ToastProps {
  message: string | null;
}

const Toast = ({ message }: ToastProps) => {
  return (
    <div
      aria-live="polite"
      aria-atomic="true"
      className="pointer-events-none fixed bottom-8 left-1/2 z-40 -translate-x-1/2"
    >
      {message && (
        <div className="rounded-full bg-[#13243a] px-5 py-3 text-sm text-[#EAF2F8] shadow-shell">
          {message}
        </div>
      )}
    </div>
  );
};

export default Toast;
