interface HighContrastToggleProps {
  enabled: boolean;
  onToggle: () => void;
}

const HighContrastToggle = ({ enabled, onToggle }: HighContrastToggleProps) => {
  return (
    <button
      type="button"
      className="flex items-center justify-between rounded-xl bg-[#0E1C30] px-3 py-2 focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2 focus:ring-offset-[#13243a]"
      onClick={onToggle}
    >
      <span>High contrast mode</span>
      <span
        aria-hidden="true"
        className={`inline-flex h-5 w-9 items-center rounded-full p-0.5 transition ${
          enabled ? "bg-[#26D0A4]" : "bg-[#24344d]"
        }`}
      >
        <span
          className={`block h-4 w-4 rounded-full bg-white transition ${enabled ? "translate-x-4" : ""}`}
        />
      </span>
    </button>
  );
};

export default HighContrastToggle;
