import { Dialog, Switch } from "@headlessui/react";
import { UserPrefs } from "../types";

interface AccessibilityProfileProps {
  open: boolean;
  prefs: UserPrefs;
  onUpdate: (prefs: UserPrefs) => void;
  onClose: () => void;
}

const AccessibilityProfile = ({ open, prefs, onUpdate, onClose }: AccessibilityProfileProps) => {
  function update(partial: Partial<UserPrefs>) {
    onUpdate({ ...prefs, ...partial });
  }

  return (
    <Dialog
      open={open}
      onClose={onClose}
      className="fixed inset-0 z-40 flex items-center justify-center bg-[#09172c]/80 px-4"
    >
      <Dialog.Panel className="w-full max-w-md rounded-3xl bg-[#0E1C30] px-6 py-6 text-[#EAF2F8] shadow-shell">
        <Dialog.Title className="text-lg font-semibold">Set your arrival profile</Dialog.Title>
        <Dialog.Description className="mt-2 text-sm text-[#A7B7CE]">
          Tune ClearPath for accessibility and comfort. You can change these anytime.
        </Dialog.Description>
        <div className="mt-5 space-y-4">
          <ProfileSwitch
            label="Require wheelchair accessible entrances"
            description="Only highlight doors marked wheelchair friendly."
            value={prefs.requireAccessible}
            onChange={(value) => update({ requireAccessible: value })}
          />
          <ProfileSwitch
            label="High contrast mode"
            description="Increase contrast for low light or glare."
            value={prefs.highContrast}
            onChange={(value) => update({ highContrast: value })}
          />
          <ProfileSwitch
            label="Larger buttons"
            description="Expand tap targets for gloves or mobility aids."
            value={prefs.largeButtons}
            onChange={(value) => update({ largeButtons: value })}
          />
        </div>
        <div className="mt-6 flex justify-end">
          <button
            className="rounded-full bg-[#26D0A4] px-5 py-3 text-sm font-semibold text-[#0F1A2A] focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2"
            onClick={onClose}
          >
            Save profile
          </button>
        </div>
      </Dialog.Panel>
    </Dialog>
  );
};

interface ProfileSwitchProps {
  label: string;
  description: string;
  value: boolean;
  onChange: (value: boolean) => void;
}

const ProfileSwitch = ({ label, description, value, onChange }: ProfileSwitchProps) => {
  return (
    <Switch.Group>
      <div className="flex items-start justify-between gap-4 rounded-2xl bg-[#13243a] px-4 py-4">
        <div>
          <Switch.Label className="text-sm font-semibold text-[#EAF2F8]">{label}</Switch.Label>
          <Switch.Description className="text-xs text-[#8EA0BA]">{description}</Switch.Description>
        </div>
        <Switch
          checked={value}
          onChange={onChange}
          className={`${
            value ? "bg-[#26D0A4]" : "bg-[#24344d]"
          } relative inline-flex h-7 w-12 flex-shrink-0 cursor-pointer items-center rounded-full transition`}
        >
          <span
            className={`${
              value ? "translate-x-5" : "translate-x-1"
            } inline-block h-5 w-5 transform rounded-full bg-white transition`}
          />
        </Switch>
      </div>
    </Switch.Group>
  );
};

export default AccessibilityProfile;
