import { openDB, IDBPDatabase } from "idb";
import { PlaceSuggestion, QueuedAction, UserPrefs } from "../types";

const PREF_KEY = "clearpath_prefs";
const RECENT_KEY = "clearpath_recent";
const COUNTER_KEY = "clearpath_verified_count";

type ClearPathDB = IDBPDatabase<{
  queue: QueuedAction;
  entrances: {
    id: string;
    payload: unknown;
  };
}>;

let dbPromise: Promise<ClearPathDB> | null = null;

function getDb() {
  if (!dbPromise) {
    dbPromise = openDB("clearpath-db", 1, {
      upgrade(database) {
        if (!database.objectStoreNames.contains("queue")) {
          database.createObjectStore("queue", {
            keyPath: "createdAt"
          });
        }
        if (!database.objectStoreNames.contains("entrances")) {
          database.createObjectStore("entrances", {
            keyPath: "id"
          });
        }
      }
    });
  }
  return dbPromise;
}

export function loadPrefs(): UserPrefs {
  const raw = localStorage.getItem(PREF_KEY);
  if (!raw) {
    return {
      requireAccessible: false,
      highContrast: true,
      largeButtons: false
    };
  }
  try {
    const parsed = JSON.parse(raw);
    return {
      requireAccessible: Boolean(parsed.requireAccessible),
      highContrast: Boolean(parsed.highContrast),
      largeButtons: Boolean(parsed.largeButtons)
    };
  } catch {
    return {
      requireAccessible: false,
      highContrast: true,
      largeButtons: false
    };
  }
}

export function savePrefs(prefs: UserPrefs) {
  localStorage.setItem(PREF_KEY, JSON.stringify(prefs));
}

export function loadRecents(): PlaceSuggestion[] {
  const raw = localStorage.getItem(RECENT_KEY);
  if (!raw) return [];
  try {
    return JSON.parse(raw);
  } catch {
    return [];
  }
}

export function addRecent(place: PlaceSuggestion) {
  const recents = loadRecents()
    .filter((item) => item.id !== place.id)
    .slice(0, 9);
  const updated = [place, ...recents];
  localStorage.setItem(RECENT_KEY, JSON.stringify(updated));
}

export function incrementLocalVerifiedCount() {
  const current = Number(localStorage.getItem(COUNTER_KEY) || "0");
  const next = current + 1;
  localStorage.setItem(COUNTER_KEY, String(next));
  return next;
}

export function getLocalVerifiedCount() {
  return Number(localStorage.getItem(COUNTER_KEY) || "0");
}

export async function queueAction(action: QueuedAction) {
  const db = await getDb();
  await db.put("queue", action);
}

export async function getQueuedActions() {
  const db = await getDb();
  return db.getAll("queue");
}

export async function clearQueuedAction(createdAt: number) {
  const db = await getDb();
  await db.delete("queue", createdAt);
}

export async function cacheEntrance(id: string, payload: unknown) {
  const db = await getDb();
  await db.put("entrances", { id, payload });
}

export async function getCachedEntrance(id: string) {
  const db = await getDb();
  return db.get("entrances", id);
}

export async function clearCache() {
  const db = await getDb();
  const tx = db.transaction(["queue", "entrances"], "readwrite");
  await Promise.all([tx.objectStore("queue").clear(), tx.objectStore("entrances").clear()]);
  await tx.done;
  localStorage.removeItem(RECENT_KEY);
}
