import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import Header from "./components/Header";
import SearchBox from "./components/SearchBox";
import MapView, { MapViewHandle } from "./components/MapView";
import Toast from "./components/Toast";
import BottomSheet from "./components/BottomSheet";
import AccessibilityProfile from "./components/AccessibilityProfile";
import Settings from "./components/Settings";
import ConfirmNudge from "./components/ConfirmNudge";
import EntranceActions from "./components/EntranceActions";
import { EntranceResult, PlaceSuggestion, UserPrefs } from "./types";
import {
  addRecent,
  clearCache,
  getLocalVerifiedCount,
  getQueuedActions,
  incrementLocalVerifiedCount,
  loadPrefs,
  loadRecents,
  savePrefs
} from "./lib/storage";
import { correctEntrance, fetchEntrance, replayQueuedActions, suggestPlaces, confirmEntrance } from "./lib/api";
import { openNavigation, shareDoor } from "./lib/nav";
import { vibrateArrive, vibrateNear, vibrateSave } from "./lib/haptics";

const PREFERENCES_ACK = "clearpath_prefs_seen";

interface StatusMessage {
  tone: "mint" | "amber" | "coral";
  message: string;
}

const App = () => {
  const mapRef = useRef<MapViewHandle | null>(null);
  const [prefs, setPrefs] = useState<UserPrefs>(() => loadPrefs());
  const [showProfile, setShowProfile] = useState<boolean>(() => {
    return !localStorage.getItem(PREFERENCES_ACK);
  });
  const [settingsOpen, setSettingsOpen] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [suggestions, setSuggestions] = useState<PlaceSuggestion[]>([]);
  const [recent, setRecent] = useState<PlaceSuggestion[]>(() => loadRecents());
  const [loadingSuggest, setLoadingSuggest] = useState(false);
  const [suggestError, setSuggestError] = useState<string | null>(null);
  const [selectedPlace, setSelectedPlace] = useState<EntranceResult | null>(null);
  const [status, setStatus] = useState<StatusMessage | null>(null);
  const [toast, setToast] = useState<string | null>(null);
  const [activateMap, setActivateMap] = useState(false);
  const [installPrompt, setInstallPrompt] = useState<Event | null>(null);
  const [offline, setOffline] = useState(!navigator.onLine);
  const [userLocation, setUserLocation] = useState<{ lat: number; lon: number } | null>(null);
  const [geoDenied, setGeoDenied] = useState(false);
  const [adjustMode, setAdjustMode] = useState(false);
  const [pendingEntrance, setPendingEntrance] = useState<{ lat: number; lon: number; accessible: boolean } | null>(null);
  const [localVerifications, setLocalVerifications] = useState(getLocalVerifiedCount());
  const [installVisible, setInstallVisible] = useState(false);

  useEffect(() => {
    savePrefs(prefs);
    document.documentElement.setAttribute("data-theme", prefs.highContrast ? "high-contrast" : "dark");
  }, [prefs]);

  useEffect(() => {
    const handler = (event: Event) => {
      event.preventDefault();
      setInstallPrompt(event);
      setInstallVisible(true);
    };
    window.addEventListener("beforeinstallprompt", handler);
    return () => window.removeEventListener("beforeinstallprompt", handler);
  }, []);

  useEffect(() => {
    replayQueuedActions();
  }, []);

  useEffect(() => {
    const online = () => {
      setOffline(false);
      setStatus({ tone: "mint", message: "Back online" });
      replayQueuedActions();
    };
    const offlineHandler = () => {
      setOffline(true);
      setStatus({ tone: "amber", message: "Working offline" });
    };
    window.addEventListener("online", online);
    window.addEventListener("offline", offlineHandler);
    return () => {
      window.removeEventListener("online", online);
      window.removeEventListener("offline", offlineHandler);
    };
  }, []);

  useEffect(() => {
    const watchId = navigator.geolocation.watchPosition(
      (position) => {
        const coords = {
          lat: position.coords.latitude,
          lon: position.coords.longitude
        };
        setUserLocation(coords);
        mapRef.current?.setUserLocation({ ...coords, accuracy: position.coords.accuracy });
      },
      (error) => {
        if (error.code === error.PERMISSION_DENIED) {
          setGeoDenied(true);
        }
      },
      { enableHighAccuracy: true, maximumAge: 10000, timeout: 10000 }
    );
    return () => navigator.geolocation.clearWatch(watchId);
  }, []);

  useEffect(() => {
    if (!searchValue.trim()) {
      setSuggestions([]);
      return;
    }
    const handle = window.setTimeout(async () => {
      setLoadingSuggest(true);
      try {
        const suggested = await suggestPlaces(searchValue, userLocation || undefined);
        setSuggestions(suggested);
        setSuggestError(null);
      } catch (error) {
        setSuggestError("Unable to fetch suggestions right now.");
      } finally {
        setLoadingSuggest(false);
      }
    }, 180);
    return () => window.clearTimeout(handle);
  }, [searchValue, userLocation]);

  useEffect(() => {
    if (!userLocation || !selectedPlace) return;
    const distance = distanceMeters(userLocation, {
      lat: selectedPlace.entrance.lat,
      lon: selectedPlace.entrance.lon
    });
    if (distance < 5) {
      vibrateArrive();
      mapRef.current?.highlightSnap();
      setToast("Entrance reached - enjoy the ride!");
      setTimeout(() => setToast(null), 2000);
    } else if (distance < 15) {
      vibrateNear();
    }
  }, [userLocation, selectedPlace]);

  const handleSelectSuggestion = useCallback(
    async (suggestion: PlaceSuggestion) => {
      setSearchValue(suggestion.name);
      setSuggestions([]);
      setStatus({ tone: "mint", message: "Loading entrance" });
      try {
        const entrance = await fetchEntrance(suggestion.name);
        setSelectedPlace(entrance);
        mapRef.current?.setCenter(entrance.entrance, 18);
        if (entrance.footprint?.length) {
          mapRef.current?.setFootprint(
            entrance.footprint.map((point) => [point.lon, point.lat] as [number, number])
          );
        }
        mapRef.current?.setEntrance(entrance.entrance, entrance.entrance.confidence, entrance.entrance.accessible);
        mapRef.current?.setDropoff(entrance.dropoff || null);
        mapRef.current?.setWalkways(entrance.walkSegments || []);
        setStatus({ tone: "mint", message: "Entrance refreshed" });
        setAdjustMode(false);
        setPendingEntrance(null);
        addRecent(suggestion);
        setRecent(loadRecents());
      } catch (error) {
        setStatus({ tone: "coral", message: "Could not load entrance" });
      }
    },
    []
  );

  const handleConfirm = useCallback(async () => {
    if (!selectedPlace) return;
    const response = await confirmEntrance({
      placeId: selectedPlace.id,
      entrance: { lat: selectedPlace.entrance.lat, lon: selectedPlace.entrance.lon }
    });
    if (response.ok) {
      setStatus({ tone: "mint", message: "Saved for everyone, thank you" });
      setToast("Saved for everyone, thank you");
      setTimeout(() => setToast(null), 2200);
    } else {
      setStatus({ tone: "amber", message: "Queued for sync when online" });
      setToast("Saved for everyone, thank you");
      setTimeout(() => setToast(null), 2200);
    }
    const verified = incrementLocalVerifiedCount();
    setLocalVerifications(verified);
  }, [selectedPlace]);

  const handleAdjust = useCallback(() => {
    if (!selectedPlace) return;
    setAdjustMode(true);
    setPendingEntrance((prev) => ({
      lat: prev?.lat ?? selectedPlace.entrance.lat,
      lon: prev?.lon ?? selectedPlace.entrance.lon,
      accessible: prev?.accessible ?? selectedPlace.entrance.accessible
    }));
    mapRef.current?.setAdjustMode(true, {
      radius: 25,
      center: selectedPlace.center,
      onDrag: (coords) => {
        setPendingEntrance((prev) => (prev ? { ...prev, ...coords } : { ...coords, accessible: false }));
      },
      onSnapped: () => {
        vibrateNear();
        mapRef.current?.highlightSnap();
      }
    });
  }, [selectedPlace]);

  const handleSaveAdjust = useCallback(async () => {
    if (!selectedPlace || !pendingEntrance) return;
    const payload = {
      placeId: selectedPlace.id,
      entrance: {
        lat: pendingEntrance.lat,
        lon: pendingEntrance.lon,
        accessible: pendingEntrance.accessible
      }
    };
    const response = await correctEntrance(payload);
    if (response.ok) {
      setStatus({ tone: "mint", message: "Saved for everyone, thank you" });
    } else {
      setStatus({ tone: "amber", message: "Correction queued for sync" });
    }
    vibrateSave();
    setToast("Saved for everyone, thank you");
    setTimeout(() => setToast(null), 2000);
    mapRef.current?.highlightSnap();
    setAdjustMode(false);
    mapRef.current?.setAdjustMode(false, {
      radius: 25,
      center: selectedPlace.center,
      onDrag: () => {}
    });
    const updated: EntranceResult = {
      ...selectedPlace,
      entrance: {
        ...selectedPlace.entrance,
        lat: pendingEntrance.lat,
        lon: pendingEntrance.lon,
        accessible: pendingEntrance.accessible
      }
    };
    setSelectedPlace(updated);
    mapRef.current?.setEntrance(updated.entrance, updated.entrance.confidence, updated.entrance.accessible);
  }, [pendingEntrance, selectedPlace]);

  const clearAdjust = useCallback(() => {
    setAdjustMode(false);
    setPendingEntrance(null);
    mapRef.current?.setAdjustMode(false, {
      radius: 25,
      center: selectedPlace?.center || { lat: 0, lon: 0 },
      onDrag: () => {}
    });
    if (selectedPlace) {
      mapRef.current?.setEntrance(selectedPlace.entrance, selectedPlace.entrance.confidence, selectedPlace.entrance.accessible);
    }
  }, [selectedPlace]);

  const handleInstall = useCallback(async () => {
    if (!installPrompt) return;
    setInstallVisible(false);
    const deferredPrompt = installPrompt as any;
    deferredPrompt.prompt();
    await deferredPrompt.userChoice;
  }, [installPrompt]);

  const handleNavigation = useCallback(() => {
    if (!selectedPlace) return;
    openNavigation(selectedPlace.entrance.lat, selectedPlace.entrance.lon);
  }, [selectedPlace]);

  const handleShare = useCallback(async () => {
    if (!selectedPlace) return;
    const url = `https://app.clearpath.local/#/place/${selectedPlace.id}?elat=${selectedPlace.entrance.lat}&elon=${selectedPlace.entrance.lon}`;
    await shareDoor(url);
    setToast("Link copied for sharing");
    setTimeout(() => setToast(null), 2000);
  }, [selectedPlace]);

  const handlePrefsUpdate = useCallback(
    (next: UserPrefs) => {
      setPrefs(next);
    },
    [setPrefs]
  );

  const closeProfile = useCallback(() => {
    localStorage.setItem(PREFERENCES_ACK, "1");
    setShowProfile(false);
  }, []);

  const handleLocate = useCallback(() => {
    if (!userLocation) return;
    mapRef.current?.setCenter(userLocation, 16.5);
  }, [userLocation]);

  const canSave = pendingEntrance !== null;

  const statusMessage = useMemo<StatusMessage | null>(() => {
    if (geoDenied) {
      return { tone: "coral", message: "Location denied - you can still search manually" };
    }
    return status;
  }, [geoDenied, status]);

  const verifiedTotal = selectedPlace?.verifiedCount ?? 0;
  const effectiveAccessible =
    pendingEntrance?.accessible ?? selectedPlace?.entrance.accessible ?? false;

  const handleClearCache = useCallback(async () => {
    await clearCache();
    setToast("Cache cleared");
    setTimeout(() => setToast(null), 2000);
  }, []);

  const exportCorrections = useCallback(async () => {
    const actions = await getQueuedActions();
    return JSON.stringify({ queued: actions }, null, 2);
  }, []);

  useEffect(() => {
    if (selectedPlace) {
      mapRef.current?.setEntrance(selectedPlace.entrance, selectedPlace.entrance.confidence, selectedPlace.entrance.accessible);
      mapRef.current?.setDropoff(selectedPlace.dropoff || null);
      mapRef.current?.setWalkways(selectedPlace.walkSegments || []);
    }
  }, [selectedPlace]);

  useEffect(() => {
    if (!adjustMode || !pendingEntrance) return;
    mapRef.current?.setEntrance(pendingEntrance, selectedPlace?.entrance.confidence || "medium", pendingEntrance.accessible);
  }, [adjustMode, pendingEntrance, selectedPlace]);

  return (
    <div className="relative min-h-screen w-full bg-[#09172C] text-[#EAF2F8]">
      <MapView ref={mapRef} activate={activateMap} highContrast={prefs.highContrast} />
      <Header
        highContrast={prefs.highContrast}
        requireAccessible={prefs.requireAccessible}
        onToggleContrast={() => handlePrefsUpdate({ ...prefs, highContrast: !prefs.highContrast })}
        onToggleAccessible={() =>
          handlePrefsUpdate({ ...prefs, requireAccessible: !prefs.requireAccessible })
        }
        onOpenSettings={() => setSettingsOpen(true)}
        installAvailable={installVisible}
        onInstall={handleInstall}
      />
      <main className="pointer-events-none relative z-10 flex flex-col items-center">
        <div className="w-full max-w-[680px] px-4 pointer-events-auto">
          <SearchBox
            value={searchValue}
            onChange={setSearchValue}
            onSubmit={(next) => onSubmitSearch(next, handleSelectSuggestion)}
            suggestions={suggestions}
            loading={loadingSuggest}
            error={suggestError}
            onSelect={handleSelectSuggestion}
            onActivateMap={() => setActivateMap(true)}
            recent={recent}
          />
        </div>
        {selectedPlace && (
          <div className="w-full px-4">
            <BottomSheet
              place={selectedPlace}
              status={statusMessage}
              offline={offline}
              verifiedTotal={verifiedTotal}
              localVerified={localVerifications}
            />
            <div className="mx-auto mb-8 max-w-[860px] px-4">
              <EntranceActions
                disabled={!selectedPlace}
                onNavigate={handleNavigation}
                onShare={handleShare}
                largeButtons={prefs.largeButtons}
              />
              <ConfirmNudge
                isActive={Boolean(selectedPlace)}
                onConfirm={handleConfirm}
                onAdjust={handleAdjust}
                onSave={handleSaveAdjust}
                onCancelAdjust={clearAdjust}
                canSave={canSave}
                adjustMode={adjustMode}
                largeButtons={prefs.largeButtons}
                requireAccessible={prefs.requireAccessible}
                entranceAccessible={effectiveAccessible}
                onAddAccessible={() => {
                  setPendingEntrance((prev) =>
                    prev
                      ? { ...prev, accessible: true }
                      : {
                          lat: selectedPlace.entrance.lat,
                          lon: selectedPlace.entrance.lon,
                          accessible: true
                        }
                  );
                  if (!adjustMode) {
                    handleAdjust();
                  }
                }}
                onToggleAccessible={() => {
                  setPendingEntrance((prev) => {
                    const current =
                      prev?.accessible ??
                      selectedPlace?.entrance.accessible ??
                      false;
                    return {
                      lat: prev?.lat ?? selectedPlace?.entrance.lat ?? 0,
                      lon: prev?.lon ?? selectedPlace?.entrance.lon ?? 0,
                      accessible: !current
                    };
                  });
                }}
              />
            </div>
          </div>
        )}
      </main>
      <Toast message={toast} />
      <button
        className="pointer-events-auto fixed bottom-40 right-6 flex h-12 w-12 items-center justify-center rounded-full bg-[#13243a]/90 text-[#EAF2F8] shadow-md focus:outline-none focus:ring-2 focus:ring-mint-500"
        onClick={handleLocate}
        aria-label="Locate me"
      >
        GPS
      </button>
      {installVisible && (
        <button
          className="pointer-events-auto fixed bottom-24 right-6 rounded-full bg-[#26D0A4] px-5 py-3 text-sm font-semibold text-[#0F1A2A] shadow-md focus:outline-none focus:ring-2 focus:ring-mint-500"
          onClick={handleInstall}
        >
          Install
        </button>
      )}
      <footer className="pointer-events-none fixed bottom-4 left-1/2 -translate-x-1/2 text-xs text-[#A7B7CE]">
        Map data  OpenStreetMap contributors
      </footer>
      <AccessibilityProfile
        open={showProfile}
        prefs={prefs}
        onUpdate={handlePrefsUpdate}
        onClose={closeProfile}
      />
      <Settings
        open={settingsOpen}
        onClose={() => setSettingsOpen(false)}
        onClearCache={handleClearCache}
        onExport={exportCorrections}
      />
    </div>
  );
};

function onSubmitSearch(
  value: string,
  handler: (suggestion: PlaceSuggestion) => void
) {
  if (!value) return;
  handler({
    id: value,
    name: value,
    context: "",
    lat: 0,
    lon: 0
  });
}

function distanceMeters(
  a: { lat: number; lon: number },
  b: { lat: number; lon: number }
) {
  const R = 6371000;
  const dLat = ((b.lat - a.lat) * Math.PI) / 180;
  const dLon = ((b.lon - a.lon) * Math.PI) / 180;
  const lat1 = (a.lat * Math.PI) / 180;
  const lat2 = (b.lat * Math.PI) / 180;
  const sinDLat = Math.sin(dLat / 2);
  const sinDLon = Math.sin(dLon / 2);
  const h = sinDLat * sinDLat + Math.cos(lat1) * Math.cos(lat2) * sinDLon * sinDLon;
  return 2 * R * Math.atan2(Math.sqrt(h), Math.sqrt(1 - h));
}

export default App;
