import { Dialog } from "@headlessui/react";

interface SettingsProps {
  open: boolean;
  onClose: () => void;
  onClearCache: () => void;
  onExport: () => Promise<string>;
}

const Settings = ({ open, onClose, onClearCache, onExport }: SettingsProps) => {
  async function handleExport() {
    const data = await onExport();
    const blob = new Blob([data], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const anchor = document.createElement("a");
    anchor.href = url;
    anchor.download = `clearpath-corrections-${Date.now()}.json`;
    document.body.append(anchor);
    anchor.click();
    anchor.remove();
    URL.revokeObjectURL(url);
  }

  return (
    <Dialog
      open={open}
      onClose={onClose}
      className="fixed inset-0 z-40 flex items-center justify-center bg-[#09172c]/80 px-4"
    >
      <Dialog.Panel className="w-full max-w-lg rounded-3xl bg-[#0E1C30] px-6 py-6 text-[#EAF2F8] shadow-shell">
        <Dialog.Title className="text-lg font-semibold">Settings</Dialog.Title>
        <div className="mt-5 space-y-4 text-sm text-[#A7B7CE]">
          <button
            className="w-full rounded-2xl bg-[#13243a] px-5 py-4 text-left font-semibold text-[#EAF2F8] focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2"
            onClick={onClearCache}
          >
            Clear cache
            <p className="mt-1 text-xs text-[#8EA0BA]">
              Delete saved tiles and queued confirmations. Your corrections stay on the server once
              sent.
            </p>
          </button>
          <button
            className="w-full rounded-2xl bg-[#13243a] px-5 py-4 text-left font-semibold text-[#EAF2F8] focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2"
            onClick={handleExport}
          >
            Export my corrections
            <p className="mt-1 text-xs text-[#8EA0BA]">
              Downloads a JSON file of locally queued adjustments.
            </p>
          </button>
          <div className="rounded-2xl bg-[#13243a] px-5 py-4">
            <h3 className="text-sm font-semibold text-[#EAF2F8]">About</h3>
            <p className="mt-2 text-xs">
              ClearPath uses open data from OpenStreetMap and respects community mapping efforts. We
              never sell usage data and only store anonymous confirmations.
            </p>
          </div>
          <div className="rounded-2xl bg-[#13243a] px-5 py-4">
            <h3 className="text-sm font-semibold text-[#EAF2F8]">Privacy</h3>
            <p className="mt-2 text-xs">
              No analytics, no tracking. Your preferences stay on this device. We only send entrance
              confirmations and corrections you approve.
            </p>
          </div>
        </div>
        <div className="mt-6 flex justify-end">
          <button
            className="rounded-full bg-[#26D0A4] px-5 py-3 text-sm font-semibold text-[#0F1A2A] focus:outline-none focus:ring-2 focus:ring-mint-500 focus:ring-offset-2"
            onClick={onClose}
          >
            Close
          </button>
        </div>
      </Dialog.Panel>
    </Dialog>
  );
};

export default Settings;
