const supportsVibrate = typeof navigator !== "undefined" && "vibrate" in navigator;

function tryVibrate(pattern: number[]) {
  if (!supportsVibrate) return;
  try {
    navigator.vibrate(pattern);
  } catch {
    // no-op
  }
}

export function vibrateNear() {
  tryVibrate([15]);
}

export function vibrateArrive() {
  tryVibrate([30, 20, 30]);
}

export function vibrateSave() {
  tryVibrate([60]);
}
